interface IDetailedBackupEvent extends IBackupEvent {
  fullText: string;
}

@Component({
  components: {}
})
export default class Timeline extends Mixins() {
  dialogOpen = false;
  currentEventDetails: IDetailedBackupEvent | null = null;

  showEventDetails(event: IBackupEvent) {
    this.dialogOpen = true;
    this.currentEventDetails = {
      ...event,
      fullText: `this is the full text asldfjaösklfdj alskdfjlaskjflkasjdflkaj
       lkasj flökjasöldfkjslakfj l sf
        lkaj slfkj asld alskdj
        flskajf lkf lksaj flksja flkjslfkjasthis is
        the full text asldfjaösklfdj
        alskdfjlaskjflkasjdflkaj lkasj
        flökjasöldfkjslakfj l sflkaj slfkj asld alskdj flskajf lkf lksaj flksja f
        lkjslfkjasthis is the full text asldfjaösklfdj alskdfjlaskjflkasjdflkaj lka
        sj flökjasöldfkjslakfj l sflkaj slfkj asld alskdj flskajf lkf
        lksaj flksja flkjslfkjasthis is the full text asldfjaösklfdj alskdfjlaskj
        flkasjdflkaj lkasj flökjasöldfkjslakfj l sflkaj slfkj asld alskdj flskajf l
        kf lksaj flksja flkjslfkjas`
    };
  }

  get computedEvents(): IBackupEvent[] {
    let lastDate = "";
    return [...this.events].reverse().flatMap(ev => {
      const ret: IBackupEvent[] = [];
      const event: IBackupEvent = {
        ...ev,
        time: ev.date && this.$d(ev.date, "time")
      };
      if (event.date) {
        const newDate = dateToYYYYMMDDString(event.date);
        if (newDate !== lastDate) {
          lastDate = newDate;
          const heading = (
            this.$d(parse(newDate), "short") +
            " - " +
            distanceInWordsStrict(new Date(), newDate, {
              locale:
                settingsModule.language === Language.de ? deLocale : enLocale,
              addSuffix: true,
              unit: "d"
            })
          ).toUpperCase();
          return [
            {
              id: heading,
              isSectionHeading: true,
              heading
            },
            event
          ];
        }
      }
      return event;
    });
  }

  events: IBackupEvent[] = tempEvents;

  interval = 0;

  mounted() {
    this.interval = setInterval(() => {
      this.events.push({
        id: Math.random(),
        heading: "backup job failed",
        chips: [{ name: "weekly backup job", class: "accent" }],
        email: true,
        error: true,
        dotColor: "error",
        date: new Date(),
        actions: [{ text: this.$t("timeline.resend-email").toString() }]
      });
    }, 5000);
  }

  beforeDestroy() {
    clearInterval(this.interval);
  }
}
